<template>
<div> 
  <input style="display:none"  type="file" :id="input_id" accept="image/gif,image/jpg,image/jpeg,image/png" @change="imgUpload" />
  <!--label()-->
  <slot name="label" :input_id="input_id" :picValName="picValName"></slot>   
  <slot name="displayimg" :headerImage="headerImage" ></slot> 
</div>  
</template>

<script type="text/ecmascript-6"> 
import Exif from 'exif-js' 

export default {
  name: 'imgUpload',
  props:{

  },
  data () {
    return {
      headerImage:'', //   url 
      picValue:'', // value
      picValName:'', // 
      input_id:'myImageUpload'
    }
  },
  computed:{

    //-------------------------------
    
  },
  activated(){
     
  },
  created(){
    this.$watch('headerImage',(newValue)=>{
      this.$emit('imgUrlChange',newValue)
    })
  },
  methods:{
    imgUpload(event){
      let files = event.target.files || event.dataTransfer.files;
      if(!files.length) return
      this.picValue = files[0];
      this.picValName = this.picValue.name
      this.imgPreview(this.picValue)
    },

    //   
    imgPreview(file){
      let This = this;
      let Orientation; // 

      Exif.getData(file,function(){
        //  Exif.getTag()    
        Orientation = Exif.getTag(this,'Orientation')
        // alert(Orientation) // Exif.getTag()  undefind
      })

      if(!file || !window.FileReader){ return } //     

      if(/^image/.test(file.type)){ //  fileimg  
        let reader = new FileReader(); //  fileReader
        
        reader.readAsDataURL(file);  //  dataUrl 

        reader.onload=function(){
          let result  = this.result // this  reader
          let img = new Image();  //    
          img.src = result
          

          //   10k 
          if(this.result.length <=(100*1024)){
            This.headerImage = this.result // headerImage 
            // This.imgSend()  //     
          }else{  //    10k
            img.onload = function(){
              // ( Orient)
              let data = This.compress(img,Orientation,8); 
              This.headerImage = data;
            }
          }
        }
      }
    },

    //    exif8 800
    compress(img,Orientation,rate){
      // canvas  100w
      let canvas = document.createElement('canvas');
      let ctx = canvas.getContext("2d");

      //   canvas 100w 
      let tCanvas = document.createElement('canvas');
      let tctx = tCanvas.getContext('2d');

      let initSize = img.src.length;  // 
      let width = img.width  //    (px)
      let height = img.height  //   (px)
      console.log(width,height)

      //     400
      let ratio 
      if((ratio = width*height/(rate*1000000))>1){ // w*h/4000000 >1   400w 
        console.log('larger than 400w px')
        ratio = Math.sqrt(ratio) // ratio = width*height/4000000   400w
        width /= ratio;  //     400w 
        height /= ratio;  //     400w 
      }else{
        ratio = 1 
      }

      //  canvas      400w
      canvas.width = width;
      canvas.height = height; 

      ctx.fillStyle = '#fff' //  
      ctx.fillRect(0,0,canvas.width,canvas.height); // 

      //   100  
      let count;
      if((count = width*height / 1000000)>1){  //     100w 
        console.log('100w')
        //   canvas 
        count = ~~(Math.sqrt(count)+1) //~~ count 
        let nw = ~~(width/count);  //   
        let nh = ~~(height/count); // 

        tCanvas.width = nw;
        tCanvas.height = nh;

        // 
        for (let i = 0; i < count; i++) {
          for (let j = 0; j < count; j++) {
            //  tCanvas 
            tctx.drawImage(img, i*nw*ratio, j*nh*ratio, nw*ratio, nh*ratio, 0, 0, nw, nh)
            //  tCanvas     canvas
            ctx.drawImage(tCanvas, i * nw, j * nh, nw, nh);   
          } 
        }

      }else{  //   100w 
        console.log("100w")
        ctx.drawImage(img, 0, 0, width, height);  //  canvas  w h 
      }
      

      // ios  
      if(Orientation && Orientation !==1){
        switch(Orientation){
          case 6: // 90
            this._rotateImg(img,'left',canvas);
            break;
          case 8://90  
              this._rotateImg(img,'right',canvas);  
              break;  
          case 3://180  
              this._rotateImg(img,'right',canvas);//  
              this._rotateImg(img,'right',canvas);  
              break;  
        }
      }

       //  
      let ndata = canvas.toDataURL('image/jpeg', 0.1);  
      console.log('' + initSize);  
      console.log('' + ndata.length);  
      console.log('' + ~~(100 * (initSize - ndata.length) / initSize) + "%");  
      tCanvas.width = tCanvas.height = canvas.width = canvas.height = 0;  
      return ndata;  

    },

    //   canvas
    _rotateImg(img,direction,canvas){
      //   40-30 190
      const min_step = 0;
      const max_step = 3; //   270   

      if(img == null) return // 

      let height = img.height
      let width  = img.width;
      let step = 2
      if(step ==null){
        step = min_step;
      }

      if(direction == 'right'){ // 
        step++; // step +1
        step > max_step && (step = min_step);// step3 270step  0
      }else{  // 
        step--;
        step < min_step && (step = max_step);// step3 0step  3 
      }

      let degree = step * 90 * Math.PI/180;  // step 
      let ctx = canvas.getContext('2d');

      switch (step) {      
        case 0:      
          canvas.width = width;      
          canvas.height = height;      
          ctx.drawImage(img, 0, 0);      
          break;      
        case 1:      
          canvas.width = height;      
          canvas.height = width;      
          ctx.rotate(degree);      
          ctx.drawImage(img, 0, -height);      
          break;      
        case 2:      
          canvas.width = width;      
          canvas.height = height;      
          ctx.rotate(degree);      
          ctx.drawImage(img, -width, -height);      
          break;      
        case 3:      
          canvas.width = height;      
          canvas.height = width;      
          ctx.rotate(degree);      
          ctx.drawImage(img, -width, 0);      
          break;  
      } 
    },

    // 
    clearInput(){
      this.headerImage='' 
      this.picValue='' 
      this.picValName=''
    }
    //-----------------------------
  },
  watch:{
      
  },
  components:{ 
  },
  mounted(){
     
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus" rel="stylesheet/stylus">
   
</style>

<!--
  label     slot  
<imgUpload class="img-upload has-name" @imgUrlChange="thumbNailChange">
  label
  <template slot="label" slot-scope="props">
    <label class="file-label" :for="props.input_id">
      <span class="file-cta">
        <span class="file-icon">
          <i class="fa fa-upload"></i>
        </span>
        <span class="file-label">
          
        </span>
      </span>
      <span class="file-name">
        {{props.picValName}} 
      </span>
    </label>
  </template>
  
  <template slot="displayimg" slot-scope="props">
    <div  class="show" v-show="props.headerImage">  
      <div class="picture" :style="'backgroundImage:url('+props.headerImage+')'"> 
      </div>  
    </div>
  </template> 
</imgUpload>
-->
 




